import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Dna, Brain, Home } from "lucide-react"
import Link from "next/link"
import { Button } from "@/components/ui/button"

export function PillarsSection() {
  const pillars = [
    {
      icon: Dna,
      title: "BioAlign™",
      tagline: "Full Life Optimization",
      description:
        "Comprehensive biological optimization through DNA analysis, epigenetics, biomarkers, and personalized protocols.",
      link: "/bioalign",
    },
    {
      icon: Brain,
      title: "NeuroSleep™",
      tagline: "Precision Sleep Reset",
      description: "End chronic fatigue with precision sleep science and nervous system optimization.",
      link: "/neurosleep",
    },
    {
      icon: Home,
      title: "Digital Twin",
      tagline: "Your Health Intelligence",
      description: "A living model of your health that learns, adapts, and guides your optimization journey.",
      link: "/digital-twin",
    },
  ]

  return (
    <section className="py-24 px-6 lg:px-8 bg-secondary/20">
      <div className="container mx-auto max-w-6xl">
        <div className="text-center mb-16">
          <h2 className="font-serif text-3xl sm:text-4xl md:text-5xl text-foreground mb-4">Core Pillars</h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Three interconnected systems working together for optimal results.
          </p>
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          {pillars.map((pillar, index) => (
            <Card key={index} className="border-2 hover:border-primary/50 transition-all duration-300">
              <CardHeader>
                <div className="w-12 h-12 rounded-lg bg-primary/10 flex items-center justify-center mb-4">
                  <pillar.icon className="w-6 h-6 text-primary" />
                </div>
                <CardTitle className="font-serif text-2xl">{pillar.title}</CardTitle>
                <CardDescription className="text-accent font-medium">{pillar.tagline}</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <p className="text-muted-foreground leading-relaxed">{pillar.description}</p>
                <Link href={pillar.link}>
                  <Button variant="link" className="p-0 h-auto font-medium">
                    Learn More →
                  </Button>
                </Link>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  )
}
